// sass-lint:disable-all
// Mixins
@mixin breakpoint($param1, $param2: false) {

    $declaration: "only screen and (min-width: #{$param1})";

    @if ($param2 != false) {
        $declaration: "only screen and (min-width: #{$param1}) and (max-width: #{$param2})";
    }

    @media #{$declaration} {
        @content;
    }
}
